import React from 'react';
import { ComposedChart, Line, XAxis, YAxis, Tooltip, Legend, Bar } from 'recharts';

function Chart(props) {
    return (
        <div className="Chart">
            <ComposedChart width={1200} height={400} data={props.weatherData} padding={50}>
                <XAxis dataKey="hour" />
                <YAxis dataKey="temperature" orientation="left" yAxisId="left" />
                <YAxis dataKey="precipitation" orientation="right" yAxisId="right" domain={[0, 30]} />
                <Tooltip />
                <Legend />
                <Line type="monotone" dataKey="temperature" stroke="#8884d8" yAxisId="left" />
                <Bar dataKey="precipitation" barSize={20} fill="#82ca9d" yAxisId="right" maxBarSize={2} />
            </ComposedChart>
        </div>
    );
}

export default Chart;