import React from 'react';
import './App.css';
import Chart from './Chart';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { weatherData: [],
                   location: ""}
    
    this.handleChange = this.handleChange.bind(this);
    this.fetchWeatherData = this.fetchWeatherData.bind(this);
    this.success = this.success.bind(this);
    this.error = this.error.bind(this);
    this.generateURL = this.generateURL.bind(this);
  }

  componentDidMount() {
    navigator.geolocation.getCurrentPosition(this.success, this.error);
  }

  success(position) {
    this.setState({location: Math.round(position.coords.latitude).toString() + "," + Math.round(position.coords.longitude).toString()});
    this.fetchWeatherData(true, position.coords.latitude.toString() + "," + position.coords.longitude.toString());
  }

  error() {
    this.setState({location: "Helsinki"});
    this.fetchWeatherData(false, "Helsinki");
  }

  generateURL(allowed, location) {
    if (allowed === false) {
      return "https://opendata.fmi.fi/wfs?service=WFS&version=2.0.0&request=getFeature&storedquery_id=fmi::forecast::hirlam::surface::point::multipointcoverage&place=" + location;
    } else {
      return "https://opendata.fmi.fi/wfs?service=WFS&version=2.0.0&request=getFeature&storedquery_id=fmi::forecast::hirlam::surface::point::multipointcoverage&latlon=" + location;
    }
  }

  fetchWeatherData(allowed, location) {
    var url = this.generateURL(allowed, location);

    fetch(url)
    .then((response) => {
      if (response.status !== 200) {
        console.log("Problem: " + response.status);
      } else {
        return response.text();
      }
    })
    .then((text) => {
      var parser = new DOMParser();
      var responseParsed = parser.parseFromString(text, "application/xml");
      var perHour = responseParsed.querySelector("doubleOrNilReasonTupleList").innerHTML.split("\n");
      var perHourTrimmed = perHour.slice(1, perHour.length-1).map((value) => value.trim());
      var weatherDataArray = perHourTrimmed.map((value) => value.split(" "));
      var weatherDataObjects = weatherDataArray.map(function(value, index) {
        return {hour: index, temperature: parseFloat(value[1]), precipitation: parseFloat(value[17])};
      });
      this.setState({
        weatherData: weatherDataObjects.slice(0, 24),
      });
    })
    .catch((err) => {
      console.log("Error: ", err);
    })
  }

  handleChange(event) {
    if (event.key === "Enter") {
      this.setState({location: event.target.value});
      this.fetchWeatherData(false, event.target.value);
    }
  }
  
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <input type="text" 
                 className="LocationInput" 
                 defaultValue={this.state.location} 
                 onKeyPress={this.handleChange} />
          <h2>Location: { this.state.location } </h2>
        </header>
        <body className="App-body">
          <Chart weatherData={this.state.weatherData} />
        </body>
      </div>
    );
  }
  
}

export default App;
